<template>
  <div class="logoWrap">
    <div class="beifenTip bgfff startbeifen surezjc siyao">
      <div class="loginHead flex flexcenter pre">
        <span class="iconfont icon-right-angle flexcenter" @click="goLogin"></span>
        <div class="tabul flex">
          <div class="li li1 pre" :class="tabType=='mnemonic'?'active':''" @click="tabType='mnemonic'">
            助记词
            <span class="borderBottom"></span>
          </div>
          <div class="li li2 pre" :class="tabType=='privatekey'?'active':''" @click="tabType='privatekey'">
            私钥
            <span class="borderBottom"></span>
          </div>
        </div>
      </div>
      <div v-if="tabType=='mnemonic'" class="con">
        <div class="beifenMsg">
          <div class="tishi">
            <div class="title">导入助记词</div>
          </div>
          <div class="zjcInput">
            <textarea placeholder="请输入助记词单词，并使用空格分隔" v-model="mn_word"  ></textarea>
          </div>
        </div>
        <div class="beifenMsg setpwdWrap">
          <div class="tishi">
            <div class="title" >设置密码</div>
          </div>
          <div class="qianbaopwd setpwd">
            <input type="password" placeholder="钱包密码"  v-model.trim="mn_password" />
          </div>
          <div class="cfpwd setpwd">
            <input type="password" placeholder="重复输入密码" v-model.trim="mn_repass" />
          </div>
          <div class="pwdtishi setpwd">
            <input placeholder="密码提示信息(可选)"  />
          </div>
        </div>
        <div class="nextBtn submitBtn flexcenter" @click="createWallet(1)">确定</div>
      </div>
      <div v-if="tabType=='privatekey'" class="con">
        <div class="beifenMsg">
          <div class="tishi">
            <div class="title">导入私钥</div>
          </div>
          <div class="zjcInput">
            <textarea placeholder="请输入明文私钥"  v-model.trim="pk_word"></textarea>
          </div>
        </div>
        <div class="beifenMsg setpwdWrap">
          <div class="tishi">
            <div class="title">设置密码</div>
          </div>
          <div class="qianbaopwd setpwd">
            <input type="password" placeholder="钱包密码" v-model.trim="pk_password"/>
          </div>
          <div class="cfpwd setpwd">
            <input type="password" placeholder="重复输入密码"  v-model.trim="pk_repass"/>
          </div>
          <div class="pwdtishi setpwd">
            <input placeholder="密码提示信息(可选)" />
          </div>
        </div>
        <div class="nextBtn submitBtn flexcenter" @click="createWallet(2)" >确定</div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { MessageBox } from 'mint-ui';
export default {
  data() {
    return {
      tabType: "mnemonic",
      wallet_list: {},
      walletInfo: {},
      mn_word:"",
      mn_name:"",
      mn_password:"",
      mn_repass:"",
      pk_word:"",
      pk_name:"",
      pk_password:"",
      pk_repass:"",
    };
  },
  components: {},
  created() {
    // let mnemonic = this.Wallet.generateMnemonic();
    // console.log("钱包助记词：", mnemonic)
    // let walletInfo = this.Wallet.createWalletFromMnemonic(mnemonic);

    //let  walletInfo = this.Wallet.createWalletFromMnemonic("slow shadow hire lock blur trim paper sausage fury pet rose range");

    // let  walletInfo = this.Wallet.createWalletFromPrv("0x1f16a5819be179135c23d90fd7e7b7177277312cd41741ab9aa04f44e4d7b1befff");
    // if(walletInfo!=null){
    //   console.log("钱包助记词：aaaaaaaaaaaaaa ", walletInfo)
    // }else{

    // }

    //console.log("钱包助记词：", this.walletDB.addresses.length)
  },
  methods:{
    goLogin(){
       this.$router.push('/login')
    },
    goHome(){
       this.$router.push('/home')
    },
	  createWallet(type) {
      console.log("createWallet ",type)
      let _this  = this;
      let walletInfo;
      if(type==1){
          var reg = new RegExp(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,21}$/);
          if(this.mn_word == ""){
            Toast({
              message: '助记词不能为空',
              position: 'middle',
              duration: 3000
            });
            return;
          }else if(!reg.test(this.mn_password)){
            Toast({
              message: '钱包密码格式不正确',
              position: 'middle',
              duration: 3000
            });  
            return;
          }else if(!reg.test(this.mn_repass)){
            Toast({
              message: '钱包确认密码格式不正确',
              position: 'middle',
              duration: 3000
            });  
            return;
          } 
          if (this.mn_password != this.mn_repass) {
            Toast({
              message: '钱包确认密码错误!',
              position: 'middle',
              duration: 3000
            });  
            return;
          }
          walletInfo = this.Wallet.createWalletFromMnemonic(this.mn_word);
          if(walletInfo==null){
            Toast({
              message: '助记词不正确',
              position: 'middle',
              duration: 3000
            });
            return;
          }
          this.ethWalletDB.deleteAllAccount();
          this.ethWalletDB.insertAccount(
            walletInfo.address,
            this.mn_name,
            "default",
            this.mn_password,
            walletInfo.mnemonicWord,
            walletInfo.privateKey,
            walletInfo.network,
            false,
            1
          );
      }else {
          var reg = new RegExp(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,21}$/);
          if(this.pk_word == ""){
            Toast({
              message: '助记词不能为空',
              position: 'middle',
              duration: 3000
            });
            return;
          }else if(!reg.test(this.pk_password)){
            Toast({
              message: '钱包密码格式不正确',
              position: 'middle',
              duration: 3000
            });  
            return;
          }else if(!reg.test(this.pk_repass)){
            Toast({
              message: '钱包确认密码格式不正确',
              position: 'middle',
              duration: 3000
            });  
            return;
          } 
          if (this.pk_password != this.pk_repass) {
            Toast({
              message: '钱包确认密码错误',
              position: 'middle',
              duration: 3000
            });  
            return;
          }
          walletInfo = this.Wallet.createWalletFromPrv(this.pk_word);
          if(walletInfo==null){
            Toast({
              message: '私钥不正确,请检查',
              position: 'middle',
              duration: 3000
            });
            return;
          }
          this.ethWalletDB.deleteAllAccount();
          this.ethWalletDB.insertAccount(
            walletInfo.address,
            this.pk_name,
            "default",
            this.pk_password,
            walletInfo.mnemonicWord,
            walletInfo.privateKey,
            walletInfo.network,
            false,
            1
          );
      }
      this.$store.dispatch('saveWalletDB', this.ethWalletDB).then(r => {
          MessageBox.confirm('', {
              title: '提示',
              message: '导入成功',
              showCancelButton: false,
              // confirmButtonText: 'confirm',
              // cancelButtonText: 'cancel'
          }).then(action => {
              if (action == 'confirm') { //确认的回调
                _this.goHome();
              }
          }).catch(err => {
             if (err == 'cancel') { //取消的回调
             }
          });
      })
    }  
  }
};
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
@import "../../styles/login.scss";
.startbeifen .tishi {
  margin-top: 10px;
}
.beifenTip .beifenMsg .tishi .title{
    font-size: 15px;
}
.setpwdWrap {
  .setpwd {
    padding: 20px 0 10px;
    border-bottom: 1px solid #f5f5f5;
  }
  input {
    border: none;
    outline: none;
    height: 30px;
  }
  input::placeholder {
    color: #bababa;
  }
  .tishi {
    .title {
      padding-bottom: 0px;
    }
  }
}
.siyao {
  .title {
    padding: 20px 0;
  }
  .loginHead {
    height: 55px;
    text-align: center;
  }

  .tabul {
    .active {
      color: var(--basetext) !important;
      .borderBottom {
        background-color: var(--basetext) !important;
      }
    }
    .li {
      font-size: 16px;
      //   padding-bottom: 6px;
      //   border-bottom: 3px solid #fff;
      //   top: 8px;
      .borderBottom {
        width: 80%;
        height: 3px;
        background-color: #fff;
        position: absolute;
        bottom: -6px;
        left: 0;
        right: 0;
        margin: auto;
        border-radius: 156px;
        overflow: hidden;
      }
    }
    .li2 {
      margin-left: 30px;
    }
  }
  .icon-right-angle {
    position: absolute;
    left: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  textarea {
    border: none;
    outline: none;
    resize: none;
    background-color: transparent;
    width: 100%;
    height: 100%;
  }
}
</style>



